body {
  position: relative;
  background: $bg-body;
  font-family: 'Open Sans', sans-serif;
}

a {
  @include transition(opacity .3s ease-in-out);
  &:hover {
    opacity: .65;
    color: inherit;
    text-decoration: none;
  }
  &:focus,
  &:active {
    text-decoration: none;
  }
}

input,
textarea {
  &:focus {
    outline: none;
  }
}

strong,
b {
  font-weight: 700;
}
