/* contacts form */
.contacts__form {
  width: 580px;
  margin: 0 auto;
  .form__header {
    padding: 19px 0 12px;
    border-top: 2px solid $color6;
    border-bottom: 2px solid $color6;
    text-align: center;
    margin-bottom: 38px;
    &-item {
      display: inline-block;
      vertical-align: middle;
      padding: 0 45px;
      font-size: 18px;
      color: $color3;
      &:first-child {
        border-right: 2px solid $color6;
      }
      a {
        color: $color3;
      }
    }
    .item-icon {
      display: inline-block;
      vertical-align: middle;
    }
  }
}

.form {
  &__body {
    .row {
      margin: 0 -22px;
      > [class*="col-"] {
        padding: 0 22px;
      }
    }
  }
  &__body-item {
    margin-bottom: 33px;
  }
}
.field {
  width: 100%;
  resize: none;
  @include transition(all .3s ease-in-out);
  &:focus {
    border-color: $color1;
  }
  &-name {
    font-size: 20px;
    color: $color3;
    margin-bottom: 18px;
  }
}

input.field,
textarea.field {
  border-radius: 3px;
  border: 1px solid $color7;
  padding: 14px 14px;
}

textarea.field {
  height: 178px;
  line-height: 1.4;
  margin-bottom: 15px;
}

/* form in the popup */
.popup__form {
  width: 565px;
  background: $color0;
  padding: 45px 120px 28px;
  border-radius: 10px;
  position: relative;
  .fields {
    .field-wrapper {
      margin-bottom: 18px;
      &:last-child {
        margin-bottom: 38px;
      }
    }
    .field {
      padding: 8px;
    }
  }
  .field-name {
    font-size: 16px;
    font-weight: 600;
    color: $color2;
  }
  .btn {
    margin-bottom: 32px;
  }
  .member {
    color: $color2;
    text-align: center;
    a {
      color: $color1;
      text-decoration: underline;
      &:hover {
        text-decoration: none;
        color: $color2;
      }
    }
  }
  &__title {
    color: $color2;
    font-size: 26px;
    text-align: center;
    font-weight: 600;
    margin-bottom: 38px;
    .subtext {
      display: block;
      font-weight: 400;
      font-size: 16px;
      margin-top: 12px;
    }
  }
}

/* styles for popup window and close cross */
#popup {
  transform: scale(0.8);
}
.popup_visible #popup {
  transform: scale(1);
}
.popup_close {
  position: absolute;
  top: 20px;
  right: 35px;
  width: 22px;
  height: 22px;
  opacity: 0.3;
  cursor: pointer;
  @include transition(all .3s ease-in-out);
}
.popup_close:hover {
  opacity: 1;
}
.popup_close:before,
.popup_close:after {
  position: absolute;
  left: 15px;
  content: ' ';
  height: 21px;
  width: 2px;
  background-color: #333;
}
.popup_close:before {
  transform: rotate(45deg);
}
.popup_close:after {
  transform: rotate(-45deg);
}
