//bootstrap restyle
@media (min-width: 1200px) {
  .container {
    width: 1264px;
  }
}

//bootstrap col`s with equal heights
.row {
  &.eq-height {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 45px;
    > [class*="col-"] {
      display: flex;
      flex-direction: column;
    }
  }
}
