.btn {
  font-size: 16px;
  font-weight: 400;
  color: $color0;
  border: 1px solid $color0;
  border-radius: 10px;
  text-align: center;
  padding: 8px 18px;
  top: 0;
  @include transition(opacity .3s ease-in-out);
  &:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 5px;
    height: 5px;
    background: rgba(255, 255, 255, .5);
    opacity: 0;
    border-radius: 100%;
    transform: scale(1, 1) translate(-50%);
    transform-origin: 50% 50%;
  }
  &:hover {
    color: $color0;
    opacity: .8;
  }
  &:focus,
  &:active {
    color: $color0;
    outline: 0 !important;
    box-shadow: none;
  }
  &-transparent {
    background: transparent;
  }
  &-color {
    background: $color1;
    border-color: transparent;
    @include transition(all .3s ease-in-out);
    &:hover {
      top: -4px;
      box-shadow: 0 3px 6px 0 rgba(0,0,0,.55);
    }
  }
  &-color2 {
    @extend .btn-color;
    background: $color1_1;
    border-color: transparent;
  }
  &-login {
    &:hover {
      opacity: .55;
    }
  }
  &-signup {
    font-weight: 600;
    padding: 18px 28px 12px;
  }
  &-signup-mod {
    @extend .btn-signup;
    padding: 18px 28px;
  }
  &-submit {
    @extend .btn-transparent;
    padding: 8px 38px;
    font-size: 18px;
    border: 2px solid $color6;
    color: $color6;
    @include transition(all .3s ease-in-out);
    &:hover {
      color: $color6;
      top: -4px;
      box-shadow: 0 3px 6px 0 rgba(0,0,0,.55);
    }
    &:focus,
    &:active {
      color: $color6;
    }
  }
  &-block {
    display: block;
  }
  &__text {
    display: block;
    &.text-first,
    &.text-second {
      display: block;
    }
    &.text-first {
      font-size: 20px;
      margin-bottom: 4px;
    }
    &.text-second {
      font-size: 12px;
      text-transform: uppercase;
      opacity: .7;
    }
  }
}

/* ripple effects on click */
.ripple {
  position: relative;
  overflow: hidden;
}
.ripple:after {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  pointer-events: none;
  background-image: radial-gradient(circle, #000 10%, transparent 10.01%);
  background-repeat: no-repeat;
  background-position: 50%;
  transform: scale(10, 10);
  opacity: 0;
  transition: transform .5s, opacity 1s;
}
.ripple:active:after {
  transform: scale(0, 0);
  opacity: .2;
  transition: 0s;
}

