.bg__city {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 1292px;
  background: url("../images/city-bg.jpg") no-repeat;
  background-size: cover;
  background-position: bottom;
  z-index: -1;
}


.main-content {
  overflow: hidden;
  padding-top: 140px;
}

/* text on the top */
.topinfo {
  color: $color0;
  width: 760px;
  text-align: center;
  margin: 0 auto 102px;
  &__title {
    font-size: 50px;
    line-height: 1.4;
    font-weight: 400;
    margin-bottom: 36px;
  }
  &__text {
    font-size: 22px;
    line-height: 1.6;
    margin-bottom: 75px;
  }
}

/* screen cashmanic */
.cashm-screen {
  width: 1344px;
  height: 855px;
  margin: 0 auto 65px;
  background: url("../images/cashm-screen.png") no-repeat;
  background-size: 100%;
  @media screen and (max-width: 1339px){
    width: 1200px;
    height: 738px;
  }
}

/* sections */
.section {
  &-white {
    padding-top: 118px;
    background: $color0;
    .blocks__item {
      margin-bottom: 40px;
    }
  }
  &__contacts {
    padding-top: 80px;
    margin-bottom: 102px;
    .section__info {
      margin-bottom: 40px;
    }
  }
  &__blocks {
    margin-bottom: 102px;
  }
  &__info {
    text-align: center;
    color: $color2;
    padding: 0 12.5%;
    margin-bottom: 102px;
  }
  &__info-mod {
    @extend .section__info;
    margin-bottom: 60px;
  }
  &__title {
    font-size: 48px;
    margin-bottom: 22px;
  }
  &__title-mod {
    font-size: 30px;
    color: $color0;
    margin-bottom: 62px;
  }
  &__text {
    font-size: 18px;
    line-height: 1.4;
  }
}


/* section blocks with text */
.blocks__item {
  color: $color3;
  position: relative;
  padding-left: 100px;
  margin-bottom: 60px;
  .item {
    &__circle {
      width: 70px;
      height: 70px;
      border-radius: 50%;
      position: absolute;
      left: 3px;
      top: 4px;
      &.circle-bg1 {
        background: $color4;
      }
      &.circle-bg2 {
        background: $color8;
      }
      &.circle-bg3 {
        background: $color5;
      }
      img {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
      }
    }
    &__title {
      font-size: 24px;
      margin-bottom: 28px;
    }
    &__text {
      font-size: 16px;
      line-height: 1.5;
    }
  }
}
.blocks__item-mod {
  @extend .blocks__item;
  padding: 117px 0 0;
  .item {
    &__circle {
      left: 50%;
      transform: translateX(-50%);
    }
    &__title {
      text-align: center;
    }
    &__text {
      text-align: justify;
    }
  }
}

/* section keynumbers */
.section__keynumbers {
  position: relative;
  padding: 68px 0 50px;
  margin-bottom: 62px;
  &:before {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: $color_op;
  }
  &.keynumbers-publisher {
    background: url("../images/pub-bg.jpg") no-repeat;
    background-size: cover;
    background-position: top;
    background-attachment: fixed;
    .keynumbers__item .item__title {
      color: $color1;
    }
  }
  &.keynumbers-advertiser {
    background: url("../images/dvv-bg.jpg") no-repeat;
    background-size: cover;
    background-position: top;
    background-attachment: fixed;
    margin-bottom: 0;
    .keynumbers__item .item__title {
      color: $color1_1;
    }
  }
  &.signup-publisher {
    background: url("../images/footer-bg.jpg") no-repeat;
    background-size: cover;
    background-position: top;
    background-attachment: fixed;
    padding: 75px 0 65px;
    margin-bottom: 0;
  }
  //keynumbers items
  .keynumbers__item {
    text-align: center;
    margin-bottom: 25px;
    .item {
      &__title {
        font-size: 48px;
        font-weight: bold;
        margin-bottom: 22px;
      }
      &__text {
        font-size: 20px;
        font-weight: 600;
        color: $color0;
        display: inline-block;
        max-width: 278px;
      }
    }
  }
}

/* block with video */
.block__video {
  width: 890px;
  height: 500px;
  margin: 0 auto 95px;
}
