.section__footer {
  background: rgba(51, 51, 51, .9);
  padding: 51px 0;
}

/* copyright sign */
.b-copyright {
  opacity: .6;
  color: $color0;
  font-size: 18px;
  .copy-sign {
    display: inline-block;
    vertical-align: top;
    font-size: 17px;
    margin-right: 2px;
  }
}

/* to top btn */
.to-top__holder {
  display: none;
  position: fixed;
  right: 45px;
  bottom: 40px;
  z-index: 999;
  a {
    position: relative;
    display: block;
    width: 50px;
    height: 50px;
    &:before {
      content: '';
      display: block;
      width: 50px;
      height: 50px;
      position: absolute;
      left: 0;
      top: 0;
      background: url('data:image/svg+xml;base64,');
      background-repeat: no-repeat;
    }
  }
}
