@keyframes fixed-header {
  0% {
    top: 0;
    opacity: 0;
    //transform: translate(0, -300px);
  }
  100% {
    top: 0;
    opacity: 1;
    //transform: translate(0, 0);
  }
}

header {
  @include transition(background .3s ease-in-out);
}

.header__logo {
  display: block;
  width: 248px;
  height: 50px;
  background: url('data:image/svg+xml;base64,') no-repeat;
  position: relative;
  z-index: 999;
}

.header__nav {
  padding: 14px 0;
  .btn-signup {
    display: none;
    font-size: 16px;
    padding: 8px 18px;
    margin-right: 10px;
  }
  .login__btn {
    padding: 5px 0;
    margin-right: 32px;
  }
}

.nav__links {
  text-align: right;
  padding: 16px 0;
  margin-left: 128px;
  li {
    padding: 0;
    margin-right: 20px;
    &:last-child {
      margin: 0;
    }
  }
  a {
    display: block;
    font-size: 18px;
    color: $color0;
  }
}

/* fixed header */
header {
  &.fixed {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: $color0;
    z-index: 1001;
    box-shadow: 0 5px 10px rgba(50, 51, 57, 0.1);
    animation: fixed-header .5s linear;
    .header__logo {
      background: url('data:image/svg+xml;base64,') no-repeat;
    }
    .nav__links a {
      color: $color2;
      position: relative;
      padding-bottom: 8px;
      @include transition(all .3s ease-in-out);
      &:before {
        content: '';
        display: block;
        position: absolute;
        left: 0;
        bottom: 0;
        width: 0;
        height: 2px;
        background: $color6;
        @include transition(all .3s ease-in);
      }
      &:hover {
        opacity: 1;
        font-weight: 700;
        &:before {
          width: 100%;
        }
      }
    }
    .btn-signup {
      display: inline-block;
    }
    .btn-transparent {
      border-color: $color2;
      color: $color2;
    }
  }
  .header__nav {

  }
}
